.profile__avatar {
  background-size: cover;
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin-right: 30px;
}
.profile__avatar:hover {
  opacity: 20%;
}

@media (min-width: 320px) and (max-width: 920px) {
  .profile__avatar {
    margin-bottom: 26px;
    margin-right: 0px;
  }
}
